@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap" rel="stylesheet);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap" rel="stylesheet);
body{
  margin: 0;
  background: linear-gradient(160deg, rgba(0,255,255,1) 0%, rgba(238,130,238,1) 79%);    
  padding: 0;
}

.auth-main-сontainer {
  display: flex;
  align-items: center; 
  justify-content: center;
  height: 100vh;
}
#clb{
  font-style:oblique;
}
.auth-modal-container {
  width: 320px;
  height: 500px;
  position: absolute;
  border-style: solid;
  border-radius: 1.5vh;
  border-width: 1px;
  border-color: #c4c7ca;
  background-color: rgba(255,255,255,0.5);
}

.auth-wrapp-loader{
  position: absolute;
  width: 100%;
  height: 100% ;
}

.auth-wrapper{
  margin: 20px;
}
.auth-logo{
  font-size: x-large;
  font-weight: bold;
  margin-top: 20px;
}
#separator{
  width: 100%;
  background-color: black;
  height: 0.5vh;
}


.auth-logo img{
  width: 200px;
  height: 125px;
}

.auth-form{
  display: flex;
  flex-direction: column;
  margin-top: 30px;
}

.auth-form > input{
  padding: 12px 20px;
  margin: 10px 0;
  border: 2px solid rgba(238,130,238,1);
  border-radius: 25px;
  background-color:#eee;
  color:#000000;
  font-family: 'Open Sans', sans-serif;
  font-size: 17px;
  font-weight: 200;
  outline:none;
}

.auth-form > button{
  padding: 12px;
  margin: 15px 0;
  border: 1px solid #00e3cf;
  transition: all 0.3s;
  border-radius: 25px;
  font-weight: 600;
  font-family: 'Open Sans', sans-serif;
  font-size: 17px;
  background-color:rgba(0,255,255,1);
  color: white;
  outline:none;
}

.auth-form > button:hover{
  border: 1px solid #00e3cf;
  background-color:white;
  color: black;
  cursor: pointer;
  transition: all 0.3s;
}

.auth-footer{
  width: 100%;
  margin-top: 20px;
  text-align: center;
  font-size: 17px;
  font-family: 'Open Sans', sans-serif;
}
.auth-footer > span{
  display: block;
  color: black;
  font-weight: 300;
  margin: 10px;
}

.auth-footer > a{
  color: black;
  border-bottom: 1px solid black;
  font-weight: 600;
  cursor: pointer;
}

.auth-footer > a:hover{
  border-bottom: none;
}
.container-loader{

  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  z-index: 100;
}

.loader {
white-space: pre-line;
  border: 6px solid #f3f3f3;
  border-top: 6px solid rgb(255, 0, 242);
  border-bottom: 6px solid rgb(255, 0, 242);
  border-radius: 50%;
  width: 35px;
  height: 35px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.user-rofile-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height:65px;
  border-bottom: solid;
  background : rgba(0,0,0,0.2)
}

.user-profile-modal{
  position: absolute;
  z-index: 5;
  background-color: white;
  width: 180px;
  right: 50px;
  top: 70px;
  box-shadow: 0 2px 5px 0 black;
}

.user-profile-modal > ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.user-profile-wrapper{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 25px;
}

.user-profile-user-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 60px;
}

.user-profile-user-info > span{
  margin-left: 15px;
  font-size: 17px;
  color: rgb(0, 0, 0);
}

.online{
  padding: 3px;
  border: 2px solid #000000;
  border-radius: 50%;
}

.user-profile-icon{
  padding: 2px 6px;
  cursor: pointer;
}

.user-profile-user-more-info{
  width: 100%;
}

.user-profile-user-more-info > a{
  transition: all 0.3s;
  display: block;
  color: black;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  font-size: 14px;
  background-color: white;
  padding: 10px;
  width: 100%;
}

.user-profile-user-more-info > a:hover{
  transition: all 0.3s;
  background-color: #e0e0e0;
}

.Overlay-user-profile {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(5, 5, 5, 0.1);
}
.avatar-as-icon{
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.avatar-as-icon > p{
  display: flex;
  width: 100%;
  height: 100%;
  color: white;
  margin: 0;
  padding: 0;
  align-items: center;
  justify-content: center;
}
.dialog-container {
  width:100%;
  height:100%;
}

.dialog-container > button{
  width: 100%;
  padding: 7px;
  transition: all 0.2s; 
  border: none;
  border-bottom: solid;
  outline: none;
  background: transparent;
}
.dialog-container > button > span{
  width: 100%;
  margin-left: 10px;
  color:#000000;
  font-size: 14px;
}

.dialog-container > button:hover{
  transition: all 0.2s;
  background: rgba(255,255,255,0.5);
}

.dialog-input-search{
  display: flex;
  flex-direction: row;
  align-items: center;
  border-bottom: solid;
  margin-top: 1px;
  padding: 5px;
  background: transparent;
}

.dialog-input-search-icon {
  margin: 0 15px;

}

.dialog-input-search > input{
  width: 90%;
  padding: 3px;
  background: transparent;
  font-size: 14px;
  outline: none;
  border: none;
  color: #000000;
}

.dialog-input-search > input::-webkit-input-placeholder{
  color: #000000;
}

.dialog-input-search > input:-ms-input-placeholder{
  color: #000000;
}

.dialog-input-search > input::placeholder{
  color: #000000;
}

.dialog-info-container{
  position: relative;
  height: calc(100vh - 231px);
}

.dialog-container ::-webkit-scrollbar{
  width: 10px;
}

.dialog-container ::-webkit-scrollbar-thumb {
  background: #cccccc; 
}
.dialog-info-container ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  
  width:100%;
}

.dialog-info-container ul li{
  height: 75px;
  width:100%;
  padding-left:15px;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-right:  solid transparent;
  cursor: pointer;
}

.dialog-info-container ul li:hover{
  cursor: pointer;
  background: rgba(255,255,255,0.5);
  border-right: 5px solid #000000;
}

.dialog-wrap-block {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin: 10px;
  justify-content: space-between;
  align-items: center;
  -webkit-font-smoothing: antialiased;
}

.dialog-wrap-block-left {
  display: flex;
  flex-direction: column;
}


.dialog-wrap-block-left span{
  display: block;
  max-width: 180px;
  color: #000000;
  font-size: 0.9em;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.dialog-wrap-block-left h5{
  color: #000000;
  font-weight: 600;
  font-size: 1em;
  max-width: 180px;
  text-overflow: ellipsis; 
  white-space: nowrap;
  overflow: hidden;
}

.dialog-wrap-block-right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.dialog-wrap-block-right p {
  margin: 0;
  padding: 0;
  color: #000000;
  font-size: 0.9em;
  margin: 0 0 5px 0;
}

.dialog-wrap-block-right span {
  display: flex;
  border-radius: 50%;
  font-size: 0.9em;
  color: white;
  font-weight: 600;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  background-color: #228d1c;
}


.dialog-no-chats {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
