  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background:linear-gradient(160deg, rgba(0,255,255,1) 0%, rgba(238,130,238,1) 79%);;
}

.home-active-container{
  width: 91%;
  min-width: 360;
  min-height: 640;
  background: rgba(255,255,255,0.5);
}

