.container-loader{

  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  z-index: 100;
}

.loader {
white-space: pre-line;
  border: 6px solid #f3f3f3;
  border-top: 6px solid rgb(255, 0, 242);
  border-bottom: 6px solid rgb(255, 0, 242);
  border-radius: 50%;
  width: 35px;
  height: 35px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}