.chat-container{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  align-items: center;
}

    .chat-container > header {
        display: flex;
        flex-direction: row;
        padding: 0 15px;
        width: 100%;
        height: 65px;
        background: rgba(0,0,0,0.2);
        align-items: center;
    }

.chat-container > header > h3 {
  margin: 0 0 0 10px;
  padding: 0;
  display: block;
  font-size: 1.3em;
  font-weight: 400;
  color: white;
}

.chat-container > header > button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 5px;
  width: 65px;
  margin-right: 15px;
  background-color: transparent;
}

.chat-container > .chat-body{
  width: 100%;
  height: Calc(100vh - 156px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.chat-container > .chat-no-selected-dialog{
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 70px;
}

.chat-container > .chat-no-selected-dialog > p{
  font-size: 1.3em;
}

.chat-container > .chat-body ::-webkit-scrollbar{
  width: 10px;
}

.chat-container > .chat-body ::-webkit-scrollbar-thumb {
  background: #cccccc; 
}














